import gsap from "gsap"

class Mouse 
{
    constructor(experience)
    {
        this.experience = experience

        // Local properties
        this.deltaY = 0
        this.scroll = 0
        this.previousScroll = 0
        this.scrollSpeed = null

        this.meshes = this.experience.world.images.meshesArray
        this.imageGroup = this.experience.world.images.group
        this.frequency = this.experience.world.images.imageWidth

        this.title = document.querySelector('.project-title')
        this.projectNumber = document.querySelector('.project-number')

        // this.mouseMove()
        // this.mouseWheel()
        // this.mouseScroll()

    }

    mouseScroll()
    {
        const scrollEvent = window.addEventListener('scroll', ()=>
        {
            this.scroll = window.scrollY / this.experience.sizes.height
            
            this.imageGroup.rotation.y = -this.scroll * 0.8

            this.scrollSpeed = this.scroll - this.previousScroll
            this.previousScroll = this.scroll


            if(this.scroll < 1.5)
            {
                gsap.to(this.experience.scene.background, {duration:1, r:0.26, g:0.12, b:0.15})
                this.animateText("RED", "01")
            }

            if(this.scroll >= 1.5 && this.scroll < 3)
            {
                gsap.to(this.experience.scene.background, {duration:1, r:0.25, g:0.34, b:0.21})
                this.animateText("GREEN", "02")
            }
            if(this.scroll >=3  && this.scroll < 4.5)
            {
                gsap.to(this.experience.scene.background, {duration:1, r:0.87, g:0.78, b:0.4})
                this.animateText("YELLOW", "03")
            }
            if(this.scroll >=4.5 && this.scroll < 6)
            {
                gsap.to(this.experience.scene.background, {duration:1, r:1, g:0.8, b:0.07})
                this.animateText("BRIGHT YELLOW", "04")
            }
            if(this.scroll >=6 && this.scroll < 7.5)
            {
                gsap.to(this.experience.scene.background, {duration:1, r:1, g:1, b:1})
                this.animateText("WHITE", "05")
            }

        })

        window.removeEventListener('scroll', scrollEvent)
    }

    mouseMove()
    {
        window.addEventListener('mousemove' , (e)=>
        {
        })
    }

    mouseWheel()
    {
        const wheelEvent = window.addEventListener('wheel', ()=>
        {
            for(const mesh of this.meshes)
            {
                gsap.to(mesh.material.uniforms.uAmplify, {value: this.scrollSpeed * 3})
                gsap.to(mesh.material.uniforms.uAmplify, {delay:0.5, value: 0})

                gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount, {duration:0.5, value:0.01})
                gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount, {delay:0.5, duration:0.5, value:0})
            }

        })

        window.removeEventListener('wheel', wheelEvent)
    }

    animateText(title, number)
    {
        this.title.textContent = title
        this.projectNumber.textContent = number

        // gsap.to(this.title, {duration:0.3, y:-200})
        // gsap.to(this.title, {delay:0.3, duration:0.3, y:0})

        // gsap.to(this.projectNumber, {duration:0.3, y:200})
        // gsap.to(this.projectNumber, {delay:0.3, duration:0.3, y:0})
    }
}

export default Mouse