import * as THREE from 'three'
import {Reflector} from 'three/examples/jsm/objects/Reflector.js'

class ReflectorMesh
{
    constructor(experience)
    {
        this.experience = experience
        this.gui = this.experience.debug.gui

        this.createFloor()
        this.createPole(-1.5, 0, -1)
        this.createPole(1.5, 0, 1)
        this.createCylinder()
    }

    // Create floor mesh
    createFloor()
    {
      this.floor = new Reflector( new THREE.CircleBufferGeometry(4,36,1), {
        //   map:this.experience.resources.items.floorNormal,
          fog:true,
          dithering: true,
          clipBias: 0.01,
          textureWidth: window.innerWidth * window.devicePixelRatio,
          textureHeight: window.innerHeight * window.devicePixelRatio,
          color: 0x000011
      } );
      this.floor.position.y = -0.5
      this.floor.rotateX( - Math.PI / 2 );

      this.experience.scene.add(this.floor)
    }

    createPole(x, y, z)
    {
      this.pole = new Reflector( new THREE.BoxBufferGeometry(0.2,10,0.2), {
        //   map:this.experience.resources.items.floorNormal,
          clipBias: 0.01,
          textureWidth: window.innerWidth * window.devicePixelRatio,
          textureHeight: window.innerHeight * window.devicePixelRatio,
          color: 0x000011
      } );
      // this.floor.position.y = -0.4
      // this.floor.rotateX( - Math.PI / 2 );
      this.pole.position.set(x, y, z)

      // this.gui.add(this.pole.position, 'x', -2, 2, 0.01)
      // this.gui.add(this.pole.position, 'z', -2, 2, 0.01)


      this.experience.scene.add(this.pole)
    }

    createCylinder()
    {
      this.cylinder = new Reflector( new THREE.PlaneBufferGeometry(1.5, 1.5, 1.5), {
          clipBias: 0.01,
          textureWidth: window.innerWidth * window.devicePixelRatio,
          textureHeight: window.innerHeight * window.devicePixelRatio,
          color: 0x000011
      } );
      // this.floor.position.y = -0.4
      // this.floor.rotateX( - Math.PI / 2 );
      this.cylinder.rotation.y = 1
      this.cylinder.position.set(-2.5, 0, -1)

      this.experience.scene.add(this.cylinder)
    }
}

export default ReflectorMesh 