import * as THREE from 'three'
import gsap from 'gsap'

import Mouse from '../Utils/Mouse'
import Images from './Images'
import Environment from './Environment'
import Silhouette from './Silhouette'
import ReflectorMesh from './ReflectorMesh'
import Cylinder from './Cylinder'

class World
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene
        this.resources = this.experience.resources

        this.resources.on('loaded' , ()=>
        {
            this.images = new Images(this.experience)
            this.environment = new Environment(this.experience)
            this.silhouette = new Silhouette(this.experience)
            this.reflectorMesh = new ReflectorMesh(this.experience)
            this.cylinder = new Cylinder(this.experience)
            this.createSphere()
            this.createRing()
            gsap.to('.loading-screen', {duration:0.3, opacity:0, display:'none'})
            

            //Once everything is loaded, instantiate mouse events
            this.mouse = new Mouse(this.experience)
        })
    }

    createSphere()
    {
        this.sphere = new THREE.Mesh(
            new THREE.SphereBufferGeometry(5,32,32),
            new THREE.MeshStandardMaterial({color:0x100001, side:THREE.DoubleSide})
        )
        this.scene.add(this.sphere)
    }

    createRing()
    {
        this.ring = new THREE.Mesh(
            new THREE.RingBufferGeometry(3,3.1,64,64),
            new THREE.MeshBasicMaterial({color:0xffffff, side:THREE.DoubleSide})
        ) 
        this.ring.position.y = 0.2
        this.ring.rotation.x = Math.PI / 2
        this.scene.add(this.ring)
    }

    update()
    {
        if(this.images)
        {
            this.images.update()
        }
    }
}

export default World