import * as THREE from 'three'

import vertexShader from '../../shaders/vertex.glsl'
import fragmentShader from '../../shaders/fragment.glsl'

class Images
{
    constructor(experience)
    {
        this.experience = experience

        // Local properties
        this.meshesArray = []

        this.video = document.querySelector( '.video' );
        this.videoTexture = new THREE.VideoTexture( this.video );

        this.resources = [
            this.experience.resources.items.texture1,
            this.experience.resources.items.texture2,
            this.experience.resources.items.texture3,
            this.experience.resources.items.texture4,
            this.experience.resources.items.texture5
        ]

        // Create meshes group
        this.group = new THREE.Group()
        this.group.rotation.y = -0.5
        this.experience.scene.add(this.group)

        this.thetaLength = Math.PI * 2 / 5
        this.imageSize = 1.5

        // Create image instances
        this.createImage(this.resources[2], 0, this.thetaLength)
        this.createImage(this.resources[1], -this.thetaLength * 1 , this.thetaLength)
        this.createImage(this.resources[3], -this.thetaLength * 2 , this.thetaLength)
        this.createImage(this.resources[0], -this.thetaLength * 3 , this.thetaLength)
        this.createImage(this.resources[4], -this.thetaLength * 4 , this.thetaLength)
    }

    createImage(texture, thetaStart, thetaLength)
    {
        const mesh = new THREE.Mesh(
            new THREE.CylinderBufferGeometry(this.imageSize, this.imageSize, this.imageSize / 1.2, 64, 1, true, thetaStart, thetaLength),
            new THREE.ShaderMaterial({
                side: THREE.DoubleSide,
                transparent:true,
                vertexShader,
                fragmentShader,
                uniforms:{
                    uTime:{value: 0},
                    uTexture:{value: texture},
                    uAmplify:{value:0}
                }
            })
        )
        // mesh.position.x = x

        this.group.add(mesh)
        this.meshesArray.push(mesh)
    }

    update()
    {
        for(const mesh of this.meshesArray)
        {
            mesh.material.uniforms.uTime.value = this.experience.time.elapsed * 0.001
        }
    }
}

export default Images