import * as THREE from 'three'
import EventEmitter from "./EventEmitter";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'

class Resources extends EventEmitter
{
    constructor(experience , sources)
    {
        super()

        this.sources = sources

        this.items = {}
        this.toLoad = this.sources.length
        this.loaded = 0

        this.setLoader()
        this.startLoading()
    }

    setLoader()
    {
        this.loaders = {}
        this.loaders.gltfLoader = new GLTFLoader()
        this.loaders.textureLoader = new THREE.TextureLoader()
        this.loaders.cubeTextureLoader = new THREE.CubeTextureLoader()
    }

    startLoading()
    {
        for(const source of this.sources)
        {
            if(source.type === 'gltfLoader')
            {
                this.loaders.gltfLoader.load(source.path , (file)=>
                {
                    this.sourceLoaded(source, file)
                })
            }
            else if(source.type === 'textureLoader')
            {
                this.loaders.textureLoader.load(source.path , (file)=>
                {
                    this.sourceLoaded(source, file)
                })
            }
            else if(source.type === 'cubeTextureLoader')
            {
                this.loaders.cubeTextureLoader.load(source.path , (file)=>
                {
                    this.sourceLoaded(source, file)
                })
            }
        }
    }

    sourceLoaded(source , file)
    {
        const multiplier = 100 / this.toLoad + 1
        const progress = Math.floor(this.loaded * multiplier)
        const text = document.querySelector('.loading-progress')

        text.textContent = `${progress}%`

        this.items[source.name] = file

        this.loaded++

        if(this.loaded === this.toLoad)
        {
            this.trigger('loaded')
        }
    }
}

export default Resources;
