import * as THREE from 'three'
import vertexShader from '../../shaders/sVertex.glsl'
import fragmentShader from '../../shaders/sFragment.glsl'

class Silhouette
{
    constructor(experience)
    {
        this.experience = experience

        this.createSilhouette(this.experience.resources.items.silhouetteTexture, -0.5, -0.4, 1.8)
        this.createSilhouette(this.experience.resources.items.silhouetteTexture2, 0.5, -0.4, 1.5)
        this.createSilhouette(this.experience.resources.items.silhouetteTexture4, -0.5, -0.4, -1.6)
        this.createSilhouette(this.experience.resources.items.silhouetteTexture5, -0.6, -0.4, -1.8)
        this.createSilhouette(this.experience.resources.items.silhouetteTexture6, -0.7, -0.4, -1.6)
        // this.createSilhouette(this.experience.resources.items.silhouetteTexture3, 0, -0.4, 1.8)
    }

    createSilhouette(texture, x, y, z)
    {
        const geo = new THREE.PlaneBufferGeometry(0.2 * 1.2, 0.3 * 1.2, 1)

        const mesh = new THREE.Mesh(
            geo,
            new THREE.ShaderMaterial({
                side: THREE.DoubleSide,
                transparent:true,
                vertexShader,
                fragmentShader,
                uniforms:{
                    uTexture:{value: texture}
                }
            })
        )

        mesh.position.set(x,y,z)

        this.experience.scene.add(mesh)
    }
}

export default Silhouette